import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpModule } from '@angular/http';
import { IonicStorageModule } from '@ionic/storage';
// Importar paginas
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { UsuariosPage } from '../pages/usuarios/usuarios';
import { LoginPage } from '../pages/login/login';
import { UniversidadesPage } from '../pages/universidades/universidades';
import { DetalleUniversidadPage } from '../pages/detalle-universidad/detalle-universidad';
import { DetalleCarreraPage } from '../pages/detalle-carrera/detalle-carrera';
// importar providers
import { AuthProvider } from '../providers/auth/auth';
import { DataUsuariosProvider } from '../providers/data-usuarios/data-usuarios';
import { DataUniversidadesProvider } from '../providers/data-universidades/data-universidades';
import { DataCarrerasProvider } from '../providers/data-carreras/data-carreras';
import { DataAreasProvider } from '../providers/data-areas/data-areas';

@NgModule({
    declarations: [
        MyApp,
        HomePage,
        UsuariosPage,
        LoginPage,
        UniversidadesPage,
        DetalleUniversidadPage,
        DetalleCarreraPage
    ],
    imports: [
        BrowserModule,
        HttpModule,
        IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/usuarios/usuarios.module#UsuariosPageModule', name: 'UsuariosPage', segment: 'usuarios', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/detalle-carrera/detalle-carrera.module#DetalleCarreraPageModule', name: 'DetalleCarreraPage', segment: 'detalle-carrera', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/detalle-universidad/detalle-universidad.module#DetalleUniversidadPageModule', name: 'DetalleUniversidadPage', segment: 'detalle-universidad', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/universidades/universidades.module#UniversidadesPageModule', name: 'UniversidadesPage', segment: 'universidades', priority: 'low', defaultHistory: [] }
  ]
}),
        IonicStorageModule.forRoot()
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
        HomePage,
        UsuariosPage,
        LoginPage,
        UniversidadesPage,
        DetalleUniversidadPage,
        DetalleCarreraPage
    ],
    providers: [
        StatusBar,
        SplashScreen,
        {provide: ErrorHandler, useClass: IonicErrorHandler},
        AuthProvider,
        DataUsuariosProvider,
        DataUniversidadesProvider,
        DataCarrerasProvider,
        DataAreasProvider
    ]
})
export class AppModule {}



